import React from 'react'

function Feature(props){
    return(
        <div className="features">
            <h1>{props.heading}</h1>
            <p> <b className="topic">{props.topic1}</b> <span className="post">{props.post1}</span> <br/> <span className="tenure">{props.tenure1}</span> </p>
            <p> <b className="topic">{props.topic2}</b> <span className="post">{props.post2}</span> <br/> <span className="tenure">{props.tenure2}</span> </p>
        </div>
    );
}

export default Feature;